$main-font: "Philosopher", sans-serif;
$main-color: #69443c;

.items-Maps {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.item-container {
  height: 500px;
  width: 250px;
  margin: 0;
  padding: 0;
  .card-item {
    display: flex;
    justify-content: center;
    img {
      max-width: 250px;
      max-height: 270px;
      border-radius: 30px;
    }
  }
  .item-details {
    display: flex;
    flex-direction: column;
    justify-content: right;
    text-align: end;
    .prev-price {
      margin-top: 0.5rem;
      font-size: 15px;
      color: #69443c9a;
      text-decoration: line-through;
    }
    .price {
      color: #69443c;
      h3 {
        font-weight: bold;
      }
    }
    .card-title {
      color: #5a3c35;
    }
  }
  .add-basket {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1rem;
    button {
      background-color: #69443c;
      color: white;
      width: 170px;
      height: 35px;
      border-radius: 20px;
      border: none;
    }
  }
}

.add-basket-button {
  cursor: pointer;
}

.basket-empty {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.basket-item-list{
  position: absolute;
  top: 50%;
}